@import url('https://fonts.googleapis.com/css2?family=Courgette&family=Nanum+Pen+Script&display=swap');

/*Font*/
h1, h2, h3, h4, h5, button, option, select, .MyButton, header > div {font-family: 'Courgette', cursive;}
p, div, input, textarea { font-family: 'Nanum Pen Script', cursive}

body {
  background:#f6f6f6;
  display:flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin:0;
}
@media (min-width:650px){
  .App{
    width: 640px;
  }
}
@media (max-width:650px){
  .App{
    width: 90vw;
  }
}
#root {
  background:#fff;
  box-shadow: rgba(100,100,111,0.2) 0px 7px 29px 0px;
}
.App{
  min-height: 100vh;
  padding:0 20px;
}

/*MyButton*/
.MyButton {
  cursor:pointer;
  border:none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 18px;
  white-space: nowrap;
}
.MyButton_default {
  background-color:#ececec;
  color:#000;
}
.MyButton_positive {
  background-color:#64c964;
  color:#fff;
}
.MyButton_negative {
  background-color:#fd565f;
  color:#fff;
}

/*header*/
header{
  padding:20px 0;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #e2e2e2;
}

header > div {
  display: flex;
}

header .head_text {
  width: 50%;
  font-size: 25px;
  justify-content: center;
}

header .head_btn_left {
  width: 25%;
  justify-content: start;
}
header .head_btn_right {
  width: 25%;
  justify-content: end;
}

/* DiaryList */
.DiaryList .menu_wrapper {
  margin:20px 0 30px 0;
  display: flex;
  justify-content: space-between;
}
.DiaryList .menu_wrapper .right_col {
  flex-grow:1;
}
.DiaryList .menu_wrapper .right_col button {
  width: 100%;
}
.DiaryList .ControlMenu {
  margin-right: 10px;
  border:none;
  border-radius: 5px;
  background:#ececec;
  padding: 10px 20px;
  cursor:pointer;
  font-size: 18px;
}

/*DiaryItem*/
.DiaryItem{
  padding: 15px 0;
  border-bottom: 1px solid #e2e2e2;
  display: flex;
  justify-content: space-between;
}
.DiaryItem .emotion_img_wrapper {
  cursor: pointer;
  min-width: 120px;
  height:80px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
}
.DiaryItem .emotion_img_wrapper_1 {
  background-color: #64c964;
}
.DiaryItem .emotion_img_wrapper_2 {
  background-color: #9dd772;
}
.DiaryItem .emotion_img_wrapper_3 {
  background-color: #fdce17;
}
.DiaryItem .emotion_img_wrapper_4 {
  background-color: #fd8446;
}
.DiaryItem .emotion_img_wrapper_5 {
  background-color: #fd565f;
}
.DiaryItem .emotion_img_wrapper img {
  width: 50%;
}
.DiaryItem .info_wrapper {
  cursor: pointer;
  flex-grow: 1;
  margin-left: 20px;
}
.DiaryItem .diary_date {
  font-weight: bold;
  font-size:25px;
  margin-bottom: 5px;
}
.DiaryItem .diary_content_preview {
  font-size: 18px;
}
.DiaryItem .btn_wrapper {
  min-width: 70px;
}

/*Diary Editor*/
.DiaryEditor section {
  margin-bottom: 40px;
}
.DiaryEditor h4{
  font-size:22px ;
  font-weight: bold;
}
.DiaryEditor .input_box{

}
.DiaryEditor .input_date{
  border: none;
  border-radius: 5px;
  background:#ececec;
  padding:10px 20px;
  cursor: pointer;
  font-size: 20px;
}
.DiaryEditor .emotion_list_wrapper {
  display: grid;
  grid-template-columns: repeat(5, auto);
  gap: 2%;
}
.DiaryEditor textarea {
  font-size: 20px;
  box-sizing: border-box;
  width: 100%;
  min-height: 200px;
  resize: vertical;
  border:none;
  border-radius: 5px;
  background: #ececec;
  padding:20px;
}
.DiaryEditor .control_box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


/* EmotionItem */
.EmotionItem {
  cursor: pointer;
  border-radius: 5px;
  padding:20px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.EmotionItem_off {
  background: #ececec;
}

.EmotionItem_on_1 {
  background:#64c964;
  color:#fff;
}
.EmotionItem_on_2 {
  background:#9dd772;
  color:#fff;
}
.EmotionItem_on_3 {
  background:#fdce17;
  color:#fff;
}
.EmotionItem_on_4 {
  background:#fd8446;
  color:#fff;
}
.EmotionItem_on_5 {
  background:#fd565f;
  color:#fff;
}

.EmotionItem img{
  width: 50%;
  margin-bottom: 10px;
}

.EmotionItem span{
  font-size: 18px;
}

/* Diary */
.DiaryPage {

}
.DiaryPage section {
  width: 100%;
  margin-bottom: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.DiaryPage h4{
  font-size: 22px;
  font-weight: bold;
}

.DiaryPage .diary_img_wrapper{
  background:#ececec;
  width: 250px;
  height: 250px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.DiaryPage .emotion_descript{
  font-size: 25px;
  color:#fff;
}

.DiaryPage .diary_img_wrapper_1 {
  background:#64c964;
}
.DiaryPage .diary_img_wrapper_2 {
  background:#9dd772;
}
.DiaryPage .diary_img_wrapper_3 {
  background:#fdce17;
}
.DiaryPage .diary_img_wrapper_4 {
  background:#fd8446;
}
.DiaryPage .diary_img_wrapper_5 {
  background:#fd565f;
}

.DiaryPage .diary_content_wrapper {
  width: 100%;
  background: #ececec;
  border-radius: 5px;
  word-break: keep-all;
  overflow-wrap: break-word;
}
.DiaryPage .diary_content_wrapper p {
  padding: 20px;
  text-align: left;
  font-size: 20px;
}